class App1 extends React.Component {
  constructor(props) {
    super(props)
    // this.childRef = React.createRef()
    this.state = {
      history: [{
        squares: Array(9).fill(null)
      }],
      labelll: '',
      resultList:[],
      sum:0,
      fail:0,
    }
  }
  alche = (value) => {
    console.log(value,Math.random(100))
    this.state.sum++;
    let result = true;
    if(Math.random(33)*100>(value)){
        result = false;
        this.state.fail++;
    }
    this.state.resultList.unshift(result)
    this.state.resultList.unshift(<span>炼金中 ……{result?
    <span style={{color:'red',}}>成功</span>
      :
      '失败'
    }!<br /></span>)
    this.setState({
      resultList:this.state.resultList,
      sum:this.state.sum,
      fail:this.state.fail
    })
  }
  handleClear = ()=>{
    this.setState({
      resultList:[],
      sum:0,
      fail:0
    })
  }
  // handleProp2 = ()=>{
    // this.childRef.current.showModal()
  // }

  render() {
    return (
        <div>
            <IntegerStep childFun={this.alche}/> 
              <Button type="primary" onClick={this.handleClear}>重置</Button>
              {/* <Button type="primary" style={{  margin: '0 16px', }} onClick={this.handleProp2}>我要抽卡！</Button> */}
              <Modal  ></Modal>
              {/* ref={this.childRef} */}
            <div key='123'>
                {/* <Button type="primary" onClick={(value)=>this.alche(30)}>😄炼金！30%出货率</Button> <br></br> */}
                    <span>一共炼金{this.state.sum}次，{Boolean(this.state.num===0)}出货率{this.state.num===0?this.state.num:((1-(this.state.fail/this.state.sum))*100).toFixed(2) }%</span><br />
                <span>
                  {this.state.resultList}
                </span>
            </div>
        </div>
         );
        }
    }

export default App1;