import { Button, Modal,Space } from 'antd';
import React, { useState } from 'react';
import './1.css';
import likesData from '../like/like.json'
const star = ['','😄','🍪🍪','🍄🍄🍄','🍏🍏🍏🍏','⭐⭐⭐⭐⭐']
let star_mon=[[],[],[],[],[],[],]
let times=0;
let sum=[0,0,0,0,0,0]
Object.values(likesData).forEach(i => {
    star_mon[i.star].push(i.name)
});
console.log(star_mon)
const App = () => {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [label, setLabel] = useState([]);
  const [total, settotal] = useState('');
  const handleRandomMon = (id)=>{
    return  (star[id] + star_mon[id][Math.floor(Math.random()*star_mon[id].length)])
  }
  const showModal = () => {
    setIsModalVisible(true);
    let arr = []
    for(let i=0;i<11;i++){
        let res = <></>
        let star;
        let ans = Math.random(33)*100
        let list=[0,1.6,7.2+1.6,31.3+7.2+1.6,31.3+7.2+1.6+59.7,100]
        for(let i=1;i<=5;i++){
          console.log(5-i+1,ans,list[i])
         if( ans<=list[i] ){
            star=5-i+1;
            break;
         }
        }
        sum[star]++;
        res=handleRandomMon(star)
        // res =(<span>star[1]<br />star_mon[1][i]</span> )
        arr.push(<div className='square'  ><div style={{backgroundColor:star==5?'rgb(249,168,68)':star==4?'rgb(206,98,155)':star==3?'rgb(87,109,249)':star==2?'rgb(133,163,77)':''}}>  [{res}] </div> </div>)
        if((i+1)%3==0){
            arr.push(<br/>)
        }
    }
    arr.push(<span className='square'> </span>)
    setLabel(arr)
    times++;
   let str=(<span>统计数据：一共十连抽卡{times}次，五星{sum[5]}，四星{sum[4]}，二星{sum[2]}，一星{sum[1]}，哦豁~</span>)
    settotal(str)
  };
  const handleOk = () => {
    setIsModalVisible(false);
  };
  const handleCancel = () => {
    setIsModalVisible(false);
  };
  return (
    <>
      <Button style={{  margin: '0 16px', }} type="primary" onClick={showModal}>
       我要抽卡
      </Button><br></br>
      {total}
      <Modal title="基础蛋池，五星1.6% 四星7.2%三星31.3%" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
        <div className='div'>
            {label}
        </div>
      </Modal>
    </>
  );
};

export default App;
