import logo from './logo.svg';
import './App.css'; 
import "antd/dist/antd.css";
import { Radio } from 'antd';
// import Menu from './menu.js'
import Likes from './like/likes.js'
import Prob from './prob/prob.js'
import Prob2 from './prob/modal.js'
import ProbNew from './prob/modal2.js'


import  { Component } from 'react';


export default class App extends Component {
  constructor(...args) {
    super(...args);
    this.state = {
      showingTab:'like'
    };
   }
   showConponent = (v) => {
    if(v.target.value ==='a'){
      this.setState({
        showingTab:'like'
      });
    }else if(v.target.value ==='b'){
      this.setState({
        showingTab:'prob_new_up'
      })
    }else if(v.target.value ==='c'){
      this.setState({
        showingTab:'prob2'
      })
    }
   }
  //  hideConponent = () => {
  //   this.setState({
  //    isShowList3: false,
  //   });
  //  }
   render() {
    return (
    <div className="App">
      {/* <Menu></Menu> */}
      <div style={{textAlign:'left',marginBottom:10,}}>
        <Radio.Group defaultValue="a" buttonStyle="solid" onChange={value=>this.showConponent(value)}>
        <Radio.Button value="c">抽卡</Radio.Button>
          <Radio.Button value="a" >魔物好感度</Radio.Button>
          <Radio.Button value="b">抽卡（超up！）</Radio.Button>
          {/* <Radio.Button value="d" disabled>不给你点</Radio.Button> */}
        </Radio.Group>
      </div>
      { this.state.showingTab =='like' ? <Likes></Likes>
      // <List3 hideConponent={this.hideConponent} />
     : this.state.showingTab =='prob_new_up' ? <ProbNew></ProbNew>
     : this.state.showingTab =='prob2' ? <Prob2></Prob2>
     : null }
      <span></span>
    </div>
  );
  }
}
